  addDocInfo(selectdFile, fileInfo: FileInfo, docType: DocType) {
    switch (docType) {
      case DocType.Word: {
        fileInfo.source = "/platform/runtime/dfs/images/word.png"
        this.files.push(fileInfo);
        break;
      }

      case DocType.Pdf: {
        fileInfo.source = "/platform/runtime/dfs/images/pdf.png"
        this.files.push(fileInfo);
        break;
      }

      case DocType.Excel: {
        fileInfo.source = "/platform/runtime/dfs/images/excel.png";
        this.files.push(fileInfo);
        break;
      }

      case DocType.Txt: {
        fileInfo.source = "/platform/runtime/dfs/images/txt.png";
        this.files.push(fileInfo);
        break;
      }

      case DocType.Powerpoint: {
        fileInfo.source = "/platform/runtime/dfs/images/ppt.png";
        this.files.push(fileInfo);
        break;
      }

      case DocType.Other: {
        fileInfo.source = "/platform/runtime/dfs/images/file.png";
        this.files.push(fileInfo);
        break;
      }

      case DocType.Image:
        let reader = new FileReader();

        reader.readAsDataURL(selectdFile);
        reader.onload = (_event) => {
          let imgUrl = reader.result;
          if (this.injector != null) {
            let sanitizer = this.injector.get(DomSanitizer);
            fileInfo.source = sanitizer.bypassSecurityTrustResourceUrl(imgUrl);
          } else
            fileInfo.source = imgUrl;
          this.files.push(fileInfo);
        }
        break;
    }

  }

  listToString(strList: string[]) {
    let name: string = "";
    for (let i = 0; i < strList.length; i++) {
      name += strList[i] + ",";
    }
    return name;
  }


  exStorageFileList: ExStorageFileInfo[] = [];

  typeChange($event: any, file: FileInfo) {
    file.extensionName = $event.data.name;
    let exStorageFileInfo: ExStorageFileInfo = new ExStorageFileInfo;
    if (file.extensionName == this.storageExtension.defaultPropertyName) {
      if (this.exStorageFileList.filter(x => x.metadataId == file.id).length > 0)
        this.exStorageFileList.splice(this.exStorageFileList.findIndex(x => x.metadataId == file.id), 1)
    } else {
      if (this.exStorageFileList.filter(x => x.metadataId == file.id).length > 0) {
        exStorageFileInfo = this.exStorageFileList.filter(x => x.metadataId == file.id)[0];
        exStorageFileInfo.exPropertyName = file.extensionName;
      } else {
        exStorageFileInfo.metadataId = file.id;
        exStorageFileInfo.exPropertyName = file.extensionName;
        this.exStorageFileList.push(exStorageFileInfo);
      }
    }
  }

  upload(): Observable<any> {
    if (this.uploadedFileTotal != this.files.filter(x => x.isUploading || x.uploadResult).length) {
      // this.notifyService.success(<NotifyOptions>{
      //   msg: 'ä¿å­æåï¼', timeout: 3000
      // });
      this.msgService.warning('ç­ä¸ä¸ï¼ææ¡£æ­£å¨ä¸ä¼ ã');
      return of(false);
    }
    if (this.storageExtension == null)
      return of(this.uploadFileInfoList);
    else {
      if (this.exStorageFileList.length == 0)
        return of(this.uploadFileInfoList);
      else {
        const loading = this.loadService.show({
          message: 'æ­£å¨å°æä»¶ä¿å­å°' + this.storageExtension.extensionName
        });
        return this.uploadService.changeStorageInstance(this.exStorageFileList, this.rootId).pipe(
          map(res => {
            if (res != null)
              this.msgService.error('ä¿å­å°' + this.storageExtension.extensionName + "å¤±è´¥ï¼ä¸ä¼ æä»¶èªå¨ä¿å­å°" + this.storageExtension.defaultPropertyName + "å­å¨ã" + res.error.Message);
            loading.close();
            return this.uploadFileInfoList;
          }))
      }
    }
  }

  cancel() {
    if (this.uploadFileInfoList.length == 0)
      return;
    this.uploadFileInfoList.forEach(element => {
      this.metadataIdList.push(element.metadataId);
    })
    this.uploadService.cancel(this.metadataIdList, this.rootId).subscribe();

  }

  getUploadFileInfoList() {
    return this.uploadFileInfoList;
  }

  delete(file) {
    this.files.splice(this.files.indexOf(file), 1);
    if (file.uploadResult)
      this.uploadedFileTotal--;
    if (this.exStorageFileList.filter(x => x.metadataId == file.id).length > 0) {
      this.exStorageFileList.splice(this.exStorageFileList.findIndex(x => x.metadataId == file.id), 1);
    }
    // var obj:any = document.querySelector('#file');
    // obj.files.remove();
    //obj.value='';
  }

  getFileSize(size: number) {
    if (size < 102400)
      return (size / 1024).toFixed(1) + "K";
    else if (size < 1024 * 1024)
      return (size / 1024).toFixed(0) + "K";
    else if (size < 100 * 1024 * 1024)
      return (size / 1024 / 1024).toFixed(1) + "M";
    else if (size < 1024 * 1024 * 1024)
      return (size / 1024 / 1024).toFixed(0) + "M";
    else
      return (size / 1024 / 1024 / 1024).toFixed(1) + "G";
  }
