import { Injectable, Injector, ComponentFactoryResolver } from '@angular/core';
import { FileUploadComponent } from './upload/file-upload/file-upload.component';
import { BsModalService, BsModalRef } from '@farris/ui-modal';
import { UploadFileInfo } from './upload/entity/uploadfileinfo';
import { Observable, of } from 'rxjs';
import { UploadLimit } from './upload/entity/uploadlimit';


@Injectable()
export class UploadDialogService {
    constructor(private modalService: BsModalService, private componentFactoryResolver: ComponentFactoryResolver, private injector: Injector) { }

    dlg: BsModalRef;

    fileInfoList: UploadFileInfo[] = [];

    showDialog(formId: string, rootId: string) {
        let flag = 0;
        return new Promise(resolve => {
            var viewerFactory = this.componentFactoryResolver.resolveComponentFactory(FileUploadComponent);
            var viewerRef = viewerFactory.create(this.injector);

            viewerRef.instance.rootId = rootId;
            viewerRef.instance.formId = formId;

            this.dlg = this.modalService.show(viewerRef, {
                title: 'ä¸ä¼ æä»¶',
                width: 950, height: 570,
                buttons: [
                    {
                        text: 'ä¿å­', cls: 'k-button k-button-icontext k-primary', handle: () => {
                            this.dlg.content.upload().subscribe(
                                res => {
                                    if (res) {
                                        resolve(res);
                                        flag = 1;
                                        this.dlg.close();
                                    }
                                }
                            );
                        }
                    },
                    {
                        text: 'å³é­', cls: 'k-button k-button-icontext', handle: () => {
                            this.dlg.close();
                        }
                    }
                ],

                showButtons: true,
                showMaxButton: false,
                beforeClose: (): Observable<boolean> => {
                    if (flag == 0)
                        this.dlg.content.cancel();
                    return of(true);
                }
            });
        })
    }

    uploadFile(formId: string, rootId: string,oldIdList: string[]=[]) { 
        return this.uploadFileWithLimit(formId, rootId, null, oldIdList);
    }


    uploadFileWithLimit(formId: string, rootId: string, limit: UploadLimit, oldIdList: string[]=[]) {
        let flag = 0;
        return new Promise(resolve => {
            var uploadFactory = this.componentFactoryResolver.resolveComponentFactory(FileUploadComponent);
            var uploadRef = uploadFactory.create(this.injector);
            if (limit != null) {
                uploadRef.instance.fileCount = limit.fileCount;
                if (limit.fileType != null && limit.fileType != "")
                    uploadRef.instance.fileType = limit.fileType
            }

            uploadRef.instance.rootId = rootId;
            uploadRef.instance.formId = formId;
            uploadRef.instance.oldIdList=oldIdList;

            this.dlg = this.modalService.show(uploadRef, {
                title: 'ä¸ä¼ æä»¶',
                width: 950, height: 570,
                buttons: [
                    {

                        text: 'ä¿å­', cls: 'k-button k-button-icontext k-primary', handle: () => {
                            this.dlg.content.upload().subscribe(
                                res => {
                                    if (res) {
                                        resolve(res);
                                        flag = 1;
                                        this.dlg.close();
                                    }
                                }
                            );
                        }
                    },
                    {
                        text: 'å³é­', cls: 'k-button k-button-icontext', handle: () => {
                            this.dlg.close();
                        }
                    }
                ],

                showButtons: true,
                showMaxButton: false,
                beforeClose: (): Observable<boolean> => {
                    if (flag == 0)
                        this.dlg.content.cancel();
                    return of(true);
                }
            });
        })
    }
}